@use 'stylesheet.scss';
.calculator {
    display: grid;
    height: 50vh;
    grid-template-columns: repeat(4,25%);
    gap: 2px;
    padding: 0;
    margin-right: 11px;
}
.calculator button{
    font-family: Digital-7 Mono;
    font-weight: 600;
    font-size: 18px;
    height:100%;
    width: 100%;
    box-sizing: border-box;
    margin: 5px;
    border: 3px solid black;
    border-radius: 10%;
    transition: all 250ms;
    cursor: pointer;
}
.calculator button:hover,
.calculator button:focus, #equals:hover {
      font-weight: 700;
      font-style: italic;
      box-shadow: rgba(0, 0, 0, 0.1) 0 40px 120px;
      color: rgba(0, 0, 0, 0.65);
      background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(189,148,233,1) 100%);
}
.clear {
    grid-row-start: 1;
    grid-row-end: 3;
}
.equals {
    grid-column-start:1;
    grid-column-end:5;
}
.operationPad {
    background: radial-gradient(circle, rgba(48,61,113,1) 0%, rgba(91,91,111,1) 100%);
    color: white;
}
.numberPad {
    background: radial-gradient(circle, rgba(189,189,235,1) 0%, rgba(211,215,230,1) 100%);
}
#equals {
    background: radial-gradient(circle, rgba(59,89,215,1) 0%, rgba(90,85,204,1) 100%);
}
.specialPad {
    background: radial-gradient(circle, rgba(221,7,7,1) 0%, rgba(154,11,11,1) 100%);
}
.display {
    width: 300px;
    height: 50px
}
.display input {
    background: linear-gradient(90deg, rgba(59,61,68,0.842) 0%, rgba(50,85,103,1) 25%, rgba(5,93,129,1) 100%);
    border-radius: 15%;
    height: 5vh;
    margin: 3px;
    width: 100%;
    color: white;
    font-family: Digital-7 Mono;    
}
#display {
    color: rgba(255, 141, 99, 0.726);
    font-weight: 700;
}
.display input::placeholder {
    color: antiquewhite;
    font-weight: 400;
}