@use 'calculator.scss';
* {
  margin: 0;
  box-sizing: border-box;
  font-family: Digital-7 Mono;
}
.App {
  text-align: center;
}
.App-header {
  background-image: linear-gradient(
    -45deg, 
    rgb(19, 163, 229) 0%, 
    rgba(87,111,230,1) 25%, 
    rgb(124, 26, 159) 51%, 
    rgba(61, 61, 113, 0.389) 100%
);  
  @keyframes animateBG { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
  }
  background-size: 300% 300%;
  animation-name: animateBG;
  animation-duration: 20s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.wrapper {
  background: radial-gradient(circle, rgba(7,65,221,0.846673703661152) 0%, rgba(0,181,255,1) 100%);
  padding: 20px;
  border-radius: 20px;
  border:  4px solid #bccfd6;
}
.programmerInfo {
  text-decoration: none;
  font-size: 3vh;
  margin-top: 1vh;
  font-weight: 600;
  color: #b4a7a7;
  transition: all .2s ease-in-out;
  font-style: italic;
}
.programmerInfo:hover{
  transform: scale(1.1);
}
.optionButton {
  width: 100px;
  height: 30px;
  margin: 10px;
  font-size: 18px;
  background-color: #282c34;
  color: #bccfd6;
  border-radius: 5px solid black;
}
.optionButton:hover {
  background-color: #7b8496;
}
dialog {
  position: absolute;
  top: auto;
  left: auto;
  right: auto;
  border: 5px solid rgb(36, 35, 35);
  outline: 100vh solid rgba(0, 0, 0, 0.716);
  font-family: Digital-7 Mono;  
  background: radial-gradient(circle, rgba(91,91,111,1) 0%, rgba(48,61,113,1) 100%); 
  border-radius: 10px;
  color: azure;
}
dialog button,dialog input {
  color: azure;
  font-family: Digital-7 Mono;
  font-size: 20px;
  background: radial-gradient(circle, rgba(95,95,140,1) 0%, rgba(43,64,144,1) 100%);
  width: 120px;
}
dialog button:hover,dialog input:hover,
dialog button:focus,dialog input:focus {
  background: radial-gradient(circle, rgba(145,145,189,1) 0%, rgba(65,96,213,1) 100%);
}
